body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}



body, html {
  /* display: grid; */
  text-align: center;
  min-width: 200px;
  min-height: 50px;
  text-transform: uppercase;
  font-family: Arial, Helvetica, sans-serif;
  color: #292929;
  font-weight: 700;
  margin: 0;
  padding: 0;
}

/* Main Container */

.main {
  position: absolute;
  height: 100vh;
  -webkit-transform: translate(0px, 0px);
      -ms-transform: translate(0px, 0px);
          transform: translate(0px, 0px);
  width:  100vw;
  -webkit-transition: 0.35s;
  -o-transition: 0.35s;
  transition: 0.35s;
}

.main.open {
  opacity: 0.5;
  width: calc(100vw - 250px);
  -webkit-transform: translate(250px, 0px);
      -ms-transform: translate(250px, 0px);
          transform: translate(250px, 0px);
  -webkit-transition: 0.7s;
  -o-transition: 0.7s;
  transition: 0.7s;
}

@media screen and (max-width: 400px) {
  .main.open {
      opacity: 0.5;
      width: 100vw;
      -webkit-transform: translate(0px, 0px);
          -ms-transform: translate(0px, 0px);
              transform: translate(0px, 0px);
      -webkit-transition: 0.7s;
      -o-transition: 0.7s;
      transition: 0.7s;
  }
}


/* Main Content */

.title {
  display: -ms-flexbox;
  display: flex;
  color: #ffffff;
  width: calc(100% - 20px);
  height: calc(10% - 20px);
  min-height: 30px;
  padding: 10px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color:#333333;
}



.map {
  height: calc(90% - 20px);
  width: calc(100% - 20px);
  padding: 10px;
  -ms-flex-pack: center;
      justify-content: center;
  background-color:#8bdb2f;
}

/* Sidebar */

.sidebar {
  color: #ffffff;
  width: calc(250px);
  height: calc(100vh);
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #292929;
  position: relative;
  float: left;
  -webkit-transform: translate(-290px, 0px);
      -ms-transform: translate(-290px, 0px);
          transform: translate(-290px, 0px);
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  z-index: 5;
  overflow-y: auto;
}
.sidebar.open {
  -webkit-transform: translate(0px, 0px);
      -ms-transform: translate(0px, 0px);
          transform: translate(0px, 0px);
  -webkit-transition: 0.35s;
  -o-transition: 0.35s;
  transition: 0.35s;
}

/* Sidebar Content */

.sidebar-header {
  width: calc(100% - 20px);
  padding: 10px;
  background: #1aaaff;
}

.list {
  margin: 0;
  padding: 0px 0px;
  list-style: none;
  width: 90%;
}

.list-item {
  width: calc(100% - 5px);
  margin: 0px;
  padding: 15px 10px;
  cursor: pointer;
}

/* Hamburger Menu Default */

.hamburger {
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 10;
  cursor: pointer;
}

.bar {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  margin: 5px;
  height: 5px;
  width: 30px;
  background-color: #fafafa;
}


/* X on open */

.open .bar:nth-child(2) {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  opacity: 0;
}

.open .bar:first-child {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  -webkit-transform: rotate(-45deg) translate(-7px, 7px);
      -ms-transform: rotate(-45deg) translate(-7px, 7px);
          transform: rotate(-45deg) translate(-7px, 7px);
}

.open .bar:last-child {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  -webkit-transform: rotate(45deg) translate(-7px, -7px);
      -ms-transform: rotate(45deg) translate(-7px, -7px);
          transform: rotate(45deg) translate(-7px, -7px);
}

@media not all and (hover: none) {
  /* -> on Hover */
  
  .hamburger:hover .bar:nth-child(2) {
      -webkit-transition: 0.5s;
      -o-transition: 0.5s;
      transition: 0.5s;
      opacity: 1;
  }
  
  .hamburger:hover .bar:first-child {
      -webkit-transition: 0.5s;
      -o-transition: 0.5s;
      transition: 0.5s;
      -webkit-transform: rotate(45deg) translate(9px, -4px) scale(0.75, 1);
          -ms-transform: rotate(45deg) translate(9px, -4px) scale(0.75, 1);
              transform: rotate(45deg) translate(9px, -4px) scale(0.75, 1);
  }
  
  .hamburger:hover .bar:last-child {
      -webkit-transition: 0.5s;
      -o-transition: 0.5s;
      transition: 0.5s;
      -webkit-transform: rotate(-45deg) translate(9px, 4px) scale(0.75, 1);
          -ms-transform: rotate(-45deg) translate(9px, 4px) scale(0.75, 1);
              transform: rotate(-45deg) translate(9px, 4px) scale(0.75, 1);
  }

  /* <- on open and hover */

  .open:hover .bar:nth-child(2) {
      -webkit-transition: 0.5s;
      -o-transition: 0.5s;
      transition: 0.5s;
      opacity: 1;
  }

  .open:hover .bar:first-child {
      -webkit-transition: 0.5s;
      -o-transition: 0.5s;
      transition: 0.5s;
      -webkit-transform: rotate(-45deg) translate(-9px, -4px) scale(0.75, 1);
          -ms-transform: rotate(-45deg) translate(-9px, -4px) scale(0.75, 1);
              transform: rotate(-45deg) translate(-9px, -4px) scale(0.75, 1);
  }

  .open:hover .bar:last-child {
      -webkit-transition: 0.5s;
      -o-transition: 0.5s;
      transition: 0.5s;
      -webkit-transform: rotate(45deg) translate(-9px, 4px) scale(0.75, 1);
          -ms-transform: rotate(45deg) translate(-9px, 4px) scale(0.75, 1);
              transform: rotate(45deg) translate(-9px, 4px) scale(0.75, 1);
  }

  .list-item:hover {
      background: #212121;
      border-right: 10px solid #2485bd;
  }
}

.list-item.selected {
  background-color: #212121;
  border-right: 10px solid #1aaaff;
}


.select {
  width: 90%;
  background: #fafafa;
}


